.nav {
	height: 50px;
	width: 100%;
	/* background-color: #4d4d4d; */
	position: relative;
	/* justify-content: flex-end; */
	box-shadow: none;
	outline: none;
	border: none;
	background-color: rgba(255, 255, 255, 0) !important;
}

.nav > .nav-header {
	display: inline;
}

.nav > .nav-header > .nav-title {
	display: inline-block;
	font-size: 20px;
	color: #fff;
	padding: 10px 10px 10px 200px;
}

.nav > .nav-btn {
	display: none;
}

.nav > .nav-links {
	/* display: inline; */
	font-size: 18px;
	position: absolute;
	right: 20px;
	padding-right: 200px;
	word-spacing: 20px;
}

.nav > .nav-links > .nav-links {
	display: inline-block;
	padding: 13px 25px 13px 10px;
	text-decoration: none;
	color: #efefef;
}

.nav > .nav-links > a:hover {
	background-color: rgba(0, 0, 0, 0.3);
}

.nav > .nav-links > a {
	color: white;
	text-decoration: none;
	word-spacing: 20px;
	padding-right: 20px;
}

.nav > .nav-links > p {
	display: inline-block;
	padding: 10px 100px 10px 50px;
	color: white;
	text-decoration: none;
	word-spacing: 0px;
}

.nav > .nav-links > .nav-links > a {
	padding-top: 20px;
	color: white;
	text-decoration: none;
	word-spacing: 20px;
	padding-right: 20px;
}

.nav > #nav-check {
	display: none;
}

@media (max-width: 600px) {
	.nav > .nav-btn {
		display: inline-block;
		position: absolute;
		right: 0px;
		top: 0px;
	}
	.nav > .nav-btn > label {
		display: inline-block;
		width: 50px;
		height: 50px;
		padding: 13px;
	}
	.nav > .nav-btn > label:hover,
	.nav #nav-check:checked ~ .nav-btn > label {
		background-color: rgba(0, 0, 0, 0.3);
	}
	.nav > .nav-btn > label > span {
		display: block;
		width: 25px;
		height: 10px;
		border-top: 2px solid #eee;
	}
	.nav > .nav-links {
		position: absolute;
		display: block;
		width: 100%;
		background-color: #333;
		height: 0px;
		-webkit-transition: all 0.3s ease-in;
		transition: all 0.3s ease-in;
		overflow-y: hidden;
		top: 50px;
		left: 0px;
	}
	.nav > .nav-links > a {
		display: block;
		width: 100%;
		color: white !important;
	}
	.nav > #nav-check:not(:checked) ~ .nav-links {
		height: 0px;
	}
	.nav > #nav-check:checked ~ .nav-links {
		height: calc(100vh - 50px);
		overflow-y: auto;
	}
}

